import { Component, OnInit ,ElementRef, ViewChild, ContentChild,NgZone} from '@angular/core';
import { Router } from '@angular/router';
import { IonContent } from '@ionic/angular';
import { TranslateService } from '@ngx-translate/core';
import { TranslateConfigService } from 'src/app/services/translate-config.service';
import { LoggerServiceProvider } from 'src/providers/logger-service/logger-service';
import { ConfigProvider } from 'src/providers/utils/config';
import { UtilService } from 'src/providers/utils/utils.service';


@Component({
  selector: 'app-fund-allocation',
  templateUrl: './fund-allocation.page.html',
  styleUrls: ['./fund-allocation.page.scss'],
})
export class FundAllocationPage implements OnInit {
  // @ViewChild("Content") content: any;
  //@ViewChild(IonContent, {static: true}) content: IonContent;
  @ViewChild(IonContent, { static: false }) content: IonContent;
 // @ViewChild('content')  content: any;


  selectedLanguage:string;
  fundAllocationsList :any;
  isShowingLoader : any;
  errorMsg:any;
  encryptedPolicyNo :any;
  canTryAgain = false;
  selectedPolicyData : any;
  selectedPlanType : any;
  isExpanded: boolean = false;

  constructor(
    private translateConfigService: TranslateConfigService,
    public translate:TranslateService,
    public logger:LoggerServiceProvider,
    private config: ConfigProvider,
    public utils:UtilService,
    private router: Router
  ) {
    this.selectedLanguage = this.translateConfigService.getDefaultLanguage();
    this.translateConfigService.setLanguage('en');

    this.isShowingLoader  =true;
    this.selectedPolicyData =  history.state.selectedPolicy;
    this.fundAllocationsList = history.state.fundData;
    this.selectedPlanType = this.selectedPolicyData['planType'];
    this.encryptedPolicyNo = this.selectedPolicyData['encryptedPolicyNo'];
    this.config.currentPage.pageName = "FundAllocationPage";
    this.utils.analytics("fundAllocation");
  }


  ngOnInit() {
    console.log('fire fund allocation');

   // this.expand();
    //300ms animation speed
    //console.log('ionViewDidEnter ChatPage');
  }
  ionViewWillEnter(){
     //this.scrollToBottom();
  }

  navigateToFundDetails(fund){
    this.router.navigate(['fund-details'],{state : { selectedPolicy:this.selectedPolicyData,fundData:fund} });
    //this.navCtrl.push(FundDetailsPage,{selectedPolicy:this.selectedPolicyData,fundData:fund});
}

  expand(){
    if(!this.isExpanded){
      this.isExpanded = true;
      setTimeout(() => {
        if (this.content.scrollToBottom) {
          this.content.scrollToBottom(500);
        }
      }, 500);
    }else{
      this.isExpanded = false;
   }
  }


}
